@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
body{
    overflow-y: hidden;
    font-family: 'Press Start 2P', cursive; 

}
.header{
    width: 100vw;   
    height: 100px;
    background-color: blueviolet;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 5px 5px 10px 5px black;
    /* margin-bottom: 1rem; */
    border-radius: 2px;
}
.header>h1{
    color: aliceblue;
    font-size: 3rem;
    position: relative;
    bottom: 15%;
}
.header>input{
    position: relative;
    left: 35%;
    padding: 1rem;
}
.pokemon{
    display: flex;
    flex-wrap: wrap;
margin: 2rem;
justify-content: center;
overflow-y: scroll;
height: 75vh;
border: 2px solid transparent;
box-shadow: 5px 5px 10px 5px black;
border-radius: 8px;
}
.pokemon::-webkit-scrollbar{
    display: none;
}
@media (min-width:100px)  and (max-width:1090px){
    .header{
        display: flex;
        flex-direction: column;
        /* width: fit-content; */
        height: 25vh;
    }
    .header>input{
        position: relative;
        left: 1%;
        bottom: -5%;
    }
    .header>h1{
        position: relative;
        top: -10%;
    }
}
.card {
  border: 2px solid transparent;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  height: 20rem;
  width: 25rem;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  transition: all 2s ease;
  border-radius: 8px;
  box-shadow: 5px 5px 10px 10px blueviolet;
  color: blueviolet;
}
.card:hover{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

img {
  width: 200px;
  height: 150px;
}
.container {
  display: flex;
  justify-content: center;
  margin: 1rem;
}
.front {
  display: flex;
  flex-direction: column;
  align-items: center;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  position: relative;
  top: 10%;
}
.back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  bottom: 25%;
  line-height:  40px;
  /* margin: 5rem; */
  right: 10%;
  font-size: 1.5rem;

}
.name {
  font-size: 2rem;
  color: blueviolet;
  text-transform: capitalize;
}
.bug{
    background-color: red;
  }
  @media (min-width:100px) and (max-width:1090px){
    .back {
      -webkit-transform: rotateY(180deg);
              transform: rotateY(180deg);
      position: absolute;
      -webkit-backface-visibility: hidden;
              backface-visibility: hidden;
      bottom: 10%;
      line-height:  40px;
      width: 200px;
      /* margin: 5rem; */
      right: 15%;
      font-size: 1.5rem;
      display: flex;
      flex-wrap: wrap;
    
    }
    
}
